$mainColor: #EF0D33;
$secondaryColor: #ffffff;
$tertiaryColor: #444444;
$bgColor: #111517;

#neumorphismContainer{
    background-color: $bgColor;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#neumorphismWrap{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-bottom: 30px;
    width: 100%;
}

#np1{
    width: 300px;
    height: 100px;
    border: none;
    color: $secondaryColor;
    font-family: "Bebas Neue", cursive;
    font-size: 3rem;
    border-radius: 58px;
    background: $bgColor;
    box-shadow: -10px -10px 20px #1B1F21,
    10px 10px 20px #0A0D0F;
    cursor: pointer;
}

#np1:focus{
    box-shadow:-10px -10px 20px #1B1F21,
    10px 10px 20px #0A0D0F,
    inset 13px 13px 17px #0A0D0F,
    inset -13px -13px 17px #1B1F21;
    color: #EAEAEA;
    border: 4px solid $bgColor;
}

#np2{
    width: 100px;
    height: 100px;
    border: none;
    font-family: "Bebas Neue", cursive;
    color: $secondaryColor;
    font-size: 2rem;
    border-radius: 58px;
    background: $bgColor;
    box-shadow: -10px -10px 20px #1B1F21,
    10px 10px 20px #0A0D0F;
    cursor: pointer;
}

#np2:focus{
    box-shadow:
    inset 13px 13px 17px #0A0D0F,
    inset -13px -13px 17px #1B1F21;
    color: #EAEAEA;
    font-size: 2rem;
    color: $mainColor;
}


#np3{
    width: 100px;
    height: 100px;
    border: none;
    color: $secondaryColor;
    font-size: 2rem;
    border-radius: 10px;
    background: $bgColor;
    box-shadow: -10px -10px 20px #1B1F21,
    10px 10px 20px #0A0D0F;
    cursor: pointer;
    font-family: "Bebas Neue", cursive;
}

#np3:focus{
    box-shadow:-10px -10px 20px #1B1F21,
    10px 10px 20px #0A0D0F,
    inset 13px 13px 17px #0A0D0F,
    inset -13px -13px 17px #1B1F21;
    color: $mainColor;
    border: 4px solid $bgColor;
}

#np4{
    width: 300px;
    height: 100px;
    border: none;
    color: $secondaryColor;
    font-family: "Bebas Neue", cursive;
    font-size: 3rem;
    border-radius: 58px;
    background: $bgColor;
    box-shadow: -10px -10px 20px #1B1F21,
    10px 10px 20px #0A0D0F;
    cursor: pointer;
}

#np4:focus{
    box-shadow:
    inset 13px 13px 17px #0A0D0F,
    inset -13px -13px 17px #1B1F21;
    color: #EAEAEA;
    border: 4px solid $bgColor;
}


@media(max-width:1000px){
    #np1, #np4{
        width: 220px;
        height: 70px;
        font-size: 2rem;
    }
}


@media(max-width:750px){

    #neumorphismWrap{
        flex-direction: column;
        justify-content: space-around;
        min-height: 640px;
    }

    #np1, #np4{
        width: 270px;
        height: 90px;
        font-size: 3rem;
    }
}
